import React from 'react';
import { CheckoutStep } from '../../types';

interface CheckoutStepperProps {
  steps: CheckoutStep[];
  currentStep: number;
}

const CheckoutStepper: React.FC<CheckoutStepperProps> = ({ steps, currentStep }) => {
  return (
    <div className="bg-white py-6 px-4 sm:px-6 lg:px-8 border-b border-gray-200">
      <div className="max-w-4xl mx-auto">
        <div className="flex items-center justify-between">
          {steps.map((step, index) => {
            const isActive = index === currentStep;
            const isCompleted = index < currentStep;
            const isLast = index === steps.length - 1;

            return (
              <div key={step.id} className="flex items-center flex-1">
                {/* Step Circle */}
                <div className="relative flex items-center">
                  <div
                    className={`
                      flex items-center justify-center w-10 h-10 rounded-full border-2 transition-all duration-300
                      ${isCompleted 
                        ? 'bg-green-500 border-green-500 text-white' 
                        : isActive 
                        ? 'bg-blue-500 border-blue-500 text-white' 
                        : 'bg-white border-gray-300 text-gray-500'
                      }
                    `}
                  >
                    {isCompleted ? (
                      <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                        <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
                      </svg>
                    ) : (
                      <span className="text-sm font-medium">{index + 1}</span>
                    )}
                  </div>

                  {/* Step Labels */}
                  <div className="absolute top-12 left-1/2 transform -translate-x-1/2 text-center min-w-max">
                    <div
                      className={`
                        text-sm font-medium transition-colors duration-300
                        ${isActive 
                          ? 'text-blue-600' 
                          : isCompleted 
                          ? 'text-green-600' 
                          : 'text-gray-500'
                        }
                      `}
                    >
                      {step.title}
                    </div>
                    <div className="text-xs text-gray-400 mt-1 hidden sm:block">
                      {step.description}
                    </div>
                  </div>
                </div>

                {/* Progress Line */}
                {!isLast && (
                  <div className="flex-1 mx-4">
                    <div className="h-0.5 bg-gray-200 relative">
                      <div
                        className={`
                          h-full transition-all duration-500
                          ${isCompleted ? 'bg-green-500' : 'bg-gray-200'}
                        `}
                        style={{
                          width: isCompleted ? '100%' : '0%'
                        }}
                      />
                    </div>
                  </div>
                )}
              </div>
            );
          })}
        </div>

        {/* Mobile Step Description */}
        <div className="mt-6 sm:hidden">
          <div className="text-center">
            <div className="text-lg font-medium text-gray-900">
              {steps[currentStep]?.title}
            </div>
            <div className="text-sm text-gray-500 mt-1">
              {steps[currentStep]?.description}
            </div>
          </div>
        </div>

        {/* Progress Summary */}
        <div className="mt-6 text-center">
          <div className="text-sm text-gray-500">
            Step {currentStep + 1} of {steps.length}
          </div>
          <div className="mt-2">
            <div className="bg-gray-200 rounded-full h-2 max-w-xs mx-auto">
              <div
                className="bg-blue-500 h-2 rounded-full transition-all duration-500"
                style={{
                  width: `${((currentStep + 1) / steps.length) * 100}%`
                }}
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default CheckoutStepper; 